//nodes
import React, { useState } from "react";
import { ethers } from "ethers";
import axios from "axios";
import QRCode from "qrcode.react";
import styled from "styled-components";
import { useEthers } from "@usedapp/core";
//
//user
import "./App.css";
import CONTRACTADDRESS, { APIKEY } from "./Components/addresses";
// import StudentABI from "./Components/StudentABI.json";
import TeststudentABI from "./Components/teststu.json";
import iconImage from "./Components/icon.png";
import iconImage1 from "./Components/unnamed.jpg";
import iconImag from "./Components/BBT.png";
//
function App() {
  const [newStudentFirstName, setNewStudentFirstName] = useState("");
  const [newStudentLastName, setNewStudentLastName] = useState("");
  const [newStudentCourse, setNewStudentCourse] = useState("");
  const [provider, setProvider] = useState(null);
  const [newlanguage, setLanguage] = useState("");
  const [contract, setContract] = useState(null);
  const [newStudentId, setNewStudentId] = useState("");
  const [StudentId, setStudentId] = useState("");
  const [message1, setMessage1] = useState("");
  const [message, setMessage] = useState("Connect-Wallet to Mint the Student");
  const [transactionHash, setTransactionHash] = useState("");
  const [tokenadd, setTokenadd] = useState("");

  const contractAddress = CONTRACTADDRESS;
  const contractABI = TeststudentABI;
  //for connect wallet
  const { activateBrowserWallet, account, deactivate } = useEthers();
  async function connectAndActivate() {
    await connectToEthereum();
    await activateBrowserWallet();
  }
  async function disconnectWallet() {
    try {
      await deactivate();
    } catch (error) {
      console.error("Error disconnecting wallet:", error);
    }
  }
  async function connectToEthereum() {
    try {
      if (window.ethereum) {
        const provider = new ethers.providers.Web3Provider(window.ethereum);

        // Listen for chain changes
        window.ethereum.on("chainChanged", (chainId) => {
          const parsedChainId = parseInt(chainId, 16);
          if (parsedChainId !== desiredChainId) {
            alert(
              `Chain ID has changed to ${parsedChainId}. Please switch to the Seaflag Chain with chainId: ${desiredChainId}.`
            );
          }
          window.location.reload();
        });

        const desiredChainId = 8805271;

        await window.ethereum.request({ method: "eth_requestAccounts" });
        const networkId = await provider.send("net_version", []);
        if (networkId != desiredChainId) {
          const switchConfirmed = window.confirm(
            `Please switch to the Seaflag Chain with chainId: ${desiredChainId}. Do you want to switch now?`
          );

          if (switchConfirmed) {
            await window.ethereum.request({
              method: "wallet_addEthereumChain",
              params: [
                {
                  chainId: `0x${desiredChainId.toString(16)}`,
                  chainName: "Seaflag Chain",
                  nativeCurrency: {
                    name: "SFC",
                    symbol: "SFC",
                    decimals: 18,
                  },
                  rpcUrls: ["https://evm.seaflagchain.online"],
                  blockExplorerUrls: [
                    "https://blockexplorer.seaflagchain.online",
                  ],
                },
              ],
            });

            await window.ethereum.request({
              method: "wallet_switchEthereumChain",
              params: [{ chainId: `0x${desiredChainId.toString(16)}` }],
            });
          } else {
            return;
          }
        }

        setProvider(provider);

        const contract = new ethers.Contract(
          contractAddress,
          contractABI,
          provider.getSigner()
        );
        setContract(contract);

        const signer = provider.getSigner();
        const from = await signer.getAddress();
        const domain = window.location.host;
        const exampleMessage = `${domain} wants you to sign in with your Ethereum account:\n ${from}\n\nI accept the MetaMask Terms of Service: https://community.metamask.io/tos\n\nURI: https://${domain}\nVersion: 1\nChain ID: 1\nNonce: 32891757\nIssued At: 2021-09-30T16:25:24.000Z`;
        const encoder = new TextEncoder();
        const data = encoder.encode(exampleMessage);
        const msg = exampleMessage;
        const sign = await window.ethereum.request({
          method: "personal_sign",
          params: [msg, from],
        });
      } else {
        alert("Please install MetaMask or use a Web3-enabled browser.");
      }
    } catch (error) {
      alert("Error connecting to Ethereum:", error);
    }
  }

  async function getStudent() {
    try {
      console.log(StudentId);
      if (contract) {
        const result = await contract.getStudentDetails(StudentId);
        if (
          result[0] == "" &&
          result[1] == "" &&
          result[2] == "" &&
          result[3] == "" &&
          result[4] == ""
        ) {
          setMessage("Token id not exist");
        } else {
          setMessage(
            <>
              <p>First Name: {result[0]}</p>
              <p>Last Name: {result[1]}</p>
              <p>Register Number: {result[3]}</p>
              <p>Course: {result[2]}</p>
              <p>Language: {result[4]}</p>
            </>
          );
        }
      } else {
        alert("Contract not initialized.");
      }
    } catch (error) {
      alert("Error getting student:", error);
    }
  } //

  const data = {
    newStudentFirstName,
    newStudentLastName,
    newStudentCourse,
    newStudentId,
    newlanguage,
  };
  async function addStudent() {
    setMessage("Waiting for Mint");
    // console.log(data);
    try {
      // console.log(contract);
      if (contract) {
        if (
          newStudentFirstName == "" &&
          newStudentLastName == "" &&
          newStudentCourse == "" &&
          newStudentId == "" &&
          newlanguage == ""
        ) {
          alert("Don't leave empty space fill the all component");
        } else {
          const tx = await contract.mintStudent(
            newStudentFirstName,
            newStudentLastName,
            newStudentCourse,
            newStudentId,
            newlanguage
          );
          const receipt = await tx.wait();
          // console.log(receipt);
          // console.log(receipt.events[0].args.id);
          setMessage("Student minted successfully.");
          //api
          const transactionHash = tx.hash;
          // const apiKey = APIKEY;
          // const polygonScanUrl = `https://api.polygonscan.com/api?module=transaction&action=gettxreceiptstatus&txhash=${transactionHash}&apikey=${apiKey}`;
          const apiKey = APIKEY;
          const goerli = `https://api-goerli.etherscan.io/api?module=transaction&action=gettxreceiptstatus&txhash=${transactionHash}&apikey=${apiKey}`;
          const response = await axios.get(goerli);
          setTransactionHash(transactionHash);
          if (response.data.status === "1") {
            setMessage(
              `Student minted successfully. Token ID: ${receipt.events[2].args.tokenId}`
            );
            setTokenadd(receipt.events[2].args.tokenId);
            setMessage1(`Transaction Hash is : ${transactionHash}`);
          } else if (response.data.status === "0") {
            setMessage("Api execution failed.");
          } else {
            setMessage("Transaction status is unknown.");
          }
        }
        // console.log(transactionHash);
        //api
      } else {
        alert("Contract not initialized.");
      }
    } catch (error) {
      console.error("Error adding student:", error);
    }
  }
  //

  return (
    <div className="App">
      {account ? (
        <CButton onClick={() => disconnectWallet()}>
          {account.slice(0, 4)}...{account.slice(account.length - 4)}
        </CButton>
      ) : (
        <CButton1 onClick={() => connectAndActivate()}>Connect Wallet</CButton1>
      )}
      <footer
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <img class="s" src={iconImage1} alt="Icon" />
      </footer>
      <footer
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <img class="c" src={iconImage} alt="Icon" />
      </footer>

      <h1>Issuance Portal</h1>
      <Container>
        <Overlay>
          <Content>
            <h2>Single upload</h2>
            <label>Register Number</label>
            <input
              type="number"
              value={newStudentId}
              onChange={(e) => setNewStudentId(e.target.value)}
            />
            <label>First Name</label>
            <input
              type="text"
              value={newStudentFirstName}
              onChange={(e) => setNewStudentFirstName(e.target.value)}
            />
            <label>Last Name</label>
            <input
              type="text"
              value={newStudentLastName}
              onChange={(e) => setNewStudentLastName(e.target.value)}
            />
            <label>Course</label>
            <input
              type="text"
              value={newStudentCourse}
              onChange={(e) => setNewStudentCourse(e.target.value)}
            />
            <label>Language</label>
            <input
              type="text"
              value={newlanguage}
              onChange={(e) => setLanguage(e.target.value)}
            />
            <button onClick={addStudent}>Add-Student</button>
          </Content>
        </Overlay>
        <Overlay>
          <Content>
            <p>{message}</p>

            <Dis>
              <div id="qr">
                {transactionHash && (
                  <QRCode
                    value={`https://explorer.silknodes.io/testnet-dymension/tx/${transactionHash}`}
                    renderAs="svg"
                    size={128}
                  />
                )}
              </div>
            </Dis>
          </Content>
        </Overlay>
        <Overlay>
          <Content>
            <h2>Token ID</h2>
            <input
              type="number"
              value={StudentId}
              onChange={(e) => setStudentId(e.target.value)}
            />
            <button onClick={getStudent}>Verify</button>
          </Content>
        </Overlay>
      </Container>
      <Dis>
        <p>{message}</p>
        <p> {message1}</p>
      </Dis>

      <Footer>
        <h3>  2024 - Cipherdata Tech</h3>
      </Footer>
    </div>
  );
}

export default App;

const CButton = styled.button`
  cursor: pointer;
  padding: 0.7rem 2rem;
  background-color: black;
  border-radius: 50px;
  color: yellow;
  font-weight: 500;
  margin-left: 80rem;
  margin-top: 1rem;
`;
const CButton1 = styled.button`
  cursor: pointer;
  padding: 0.7rem 2rem;
  background-color: black;
  border-radius: 50px;
  color: cyan;
  font-weight: 500;
  margin-left: 80rem;
  margin-top: 1rem;
`;
const Dis = styled.div`
  display: flex;
  align-items: center;
  max-width: 1280px;
  width: 80%;
  margin: 0 auto;
  padding: 1.5rem 0;
  position: relative;
  flex-direction: column;
  flex-wrap: nowrap;
`;

const Footer = styled.footer`
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  // margin-left: 40rem;
  h3 {
    margin-left: 40rem;
  }
  img {
    height: 80px;
    width: 80px;
    margin-left: 1rem;
  }
`;
//
const Content = styled.div`
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: flex-start;
  button {
    cursor: pointer;
    margin-top: 1rem;
    padding: 0.2rem 2rem;
    background-color: black;
    border-radius: 50px;
    color: rgb(255, 255, 255);
  }
`;

const Container = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: auto;

  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const Overlay = styled.div`
  width: 20%;
  padding: 20px;
  border-radius: 25px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: center;

  @media (max-width: 768px) {
    width: 70%;
    margin-bottom: 1rem;
  }
`;
